import React from 'react';

import { HomeCardsdata } from "../../../../services/Services";

const Card = () => {
  return (

    <div className='row g-md-5 gx-3'>

      {
        HomeCardsdata.map((data, Index) => (

          <div className='col-md-4' key={Index}>

            <div className="card rounded-0">

              <div className="card-img">
                <img src={data.cardImg} className='img-fluid' alt="imgs not show" style={{ maxHeight: "300px", minHeight: "300px", height: "300px", width: "100%" }} />
              </div>

              <div className="card-body ">
              
                <h4 className='fw-bold text-wrap' style={{ color: "#004170" }}>{data.title}</h4>
                <div className='card-text py-2 home_cards_description'>{data.description}</div>

                <button className='btn btn-primary rounded-0' style={{ background: "#004170" }}>Read More</button>

              </div>
            </div>

          </div>
        ))
      }

    </div>

  )
}

export default Card